body{
  font-family:'Roboto Condensed', sans-serif;
}
html {
  --section-background-color: linear-gradient(
    to bottom left,
    rgba(17, 16, 16, 0.582),
    rgba(12, 8, 24, 0.904)
  );

  --image-gradient: linear-gradient(
    to bottom left,
    rgba(17, 16, 16, 0.678),
    rgba(12, 10, 22, 0.863)
  );

  --imp-text-color: #c770f0;
}

.purple {
  color: #c770f0 !important;
  color: var(--imp-text-color) !important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
::-webkit-scrollbar {
    width: 7px;
    height: 5px;
}

::-webkit-scrollbar-track-piece {
    background-color: #2d1950;
}

::-webkit-scrollbar-thumb {
    height: 30px;
    background-color: #2d1950;
}

@media only screen and (min-width: 991px) {
    body::-webkit-scrollbar,
    .sub-page::-webkit-scrollbar,
    .single-page-content::-webkit-scrollbar,
    .header::-webkit-scrollbar,
    .sub-page::-webkit-scrollbar-track,
    .header::-webkit-scrollbar-track {
       background: #2d1950;
    }

    body::-webkit-scrollbar-thumb,
    .sub-page::-webkit-scrollbar-thumb,
    .header::-webkit-scrollbar-thumb {
        /* background-color: rgba(17, 214, 240, 0.30); */
       background: rgba(222, 130, 235, 0.911);
       border-radius:8 px;
    }
}

